import { Component, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

@Component({
  selector: 'app-trip-list',
  templateUrl: './trip-list.component.html',
  styleUrls: ['./trip-list.component.css']
})
export class TripListComponent implements OnInit {

  constructor(breakpointObserver: BreakpointObserver) {
    breakpointObserver.observe(['(max-width: 600px)']).subscribe(result => {
        this.displayedColumns = result.matches ?
            ['position', 'name', 'weight', 'status'] :
            ['position', 'name', 'weight', 'status'];
    });
}

displayedColumns = ['position', 'name', 'weight', 'status'];
dataSource = new MatTableDataSource<Element>(ELEMENT_DATA);

  ngOnInit(): void {
  }

}


export interface Element {
  name: string;
  position: number;
  weight: number;
  status: boolean;
}

const ELEMENT_DATA: Element[] = [
  { position: 1, name: 'Car', weight: 1, status: false },
  { position: 2, name: 'Car', weight: 4, status: false },
  { position: 3, name: 'Bus', weight: 6, status: true },
  { position: 4, name: 'Bus', weight: 9, status: false },
  { position: 5, name: 'Platform Stairs', weight: 10, status: true },
  { position: 6, name: 'Platform Stairs', weight: 1, status: false },
  { position: 7, name: 'Catering truck', weight: 14, status: false },
  { position: 8, name: 'Snow-plowing', weight: 15, status: false },
  { position: 9, name: 'Snow-plowing', weight: 18, status: false },
  { position: 10, name: 'Airstairs', weight: 20, status: false },
  { position: 11, name: 'Airstairs', weight: 22, status: false },
   
];
